.Button {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  position: relative;
  overflow: hidden;
  max-width: 100%;
  height: 3rem;
  padding: 0 2rem;
  font-weight: 800;
  text-decoration: none;
  text-overflow: ellipsis;
  text-transform: uppercase;
  transition: all 0.25s ease-in-out; }
  .Button-ghost {
    border: 2px solid #fff;
    line-height: calc(3rem - 4px);
    color: #fff; }
    .Button-ghost .Button_loadingItem {
      border-color: #fff transparent transparent transparent; }
    .Button-ghost:hover {
      background-color: white;
      border-color: #fff;
      color: #00B6C8; }
    .Button-ghost.disabled {
      opacity: 0.5;
      cursor: not-allowed; }
  .Button-fill {
    line-height: 3rem;
    color: #fff;
    background-color: #00B6C8; }
    .Button-fill:hover {
      background-color: white; }
  .Button.loading {
    padding: 0 5rem 0 3rem; }
    .Button.loading .Button_loading {
      visibility: visible;
      opacity: 1; }
  .Button.disabled {
    opacity: 0.5;
    cursor: not-allowed; }
  .Button_text {
    display: block;
    width: 100%;
    text-align: center; }
  .Button_loading {
    position: absolute;
    display: block;
    top: 50%;
    right: 2.5rem;
    width: 1.5rem;
    height: 1.5rem;
    transform: translateY(-50%);
    opacity: 0;
    visibility: hidden; }
  .Button_loadingItem {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 1.5rem;
    height: 1.5rem;
    border: 2px solid #fff;
    border-radius: 50%;
    animation: loading 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #fff transparent transparent transparent; }
    .Button_loadingItem:nth-child(1) {
      animation-delay: -0.45s; }
    .Button_loadingItem:nth-child(2) {
      animation-delay: -0.3s; }
    .Button_loadingItem:nth-child(3) {
      animation-delay: -0.15s; }
  .Button .Button_icon {
    font-size: 1.25rem;
    vertical-align: sub;
    margin-right: 0.5rem;
    fill: currentColor; }

@keyframes loading {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

